@import "../App.css";

.project {
  display: flex;
  height: 1000px;
  justify-content: center;
  align-items: center;
}


.container {
  margin-left: var(--mb-container-2);
  margin-right: var(--mb-container-2);
  overflow-y: auto;
}

.header_img {
  width: 100%;
  height: 100vh;
  object-fit: cover;
}

.header_img_title {
  position: absolute;
  top: 8vh;
}

.header_title {
  font-family: var(--font-unbounded);
  font-style: normal;
  font-weight: var(--fw-300);
  font-size: 3.33vw;
  line-height: 30px;
  color: var(--white-F6F6F6);
  padding-top: 4.7vh;
}

.header_desc {
  font-family: var(--font-raleway);
  font-style: normal;
  font-weight: var(--fw-400);
  font-size: 0.97vw;
  line-height: 14px;
  color: var(--white-F6F6F6);
  padding-top: 30px;
}

.header_content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 3.89vw;
}

/* <<=============== левый блок контента с информацией===============>> */

.header_content_left {
  width: 70%;
  overflow: hidden;
}

.title_block {
  display: flex;
  align-items: center;
  gap: 1.39vw;
}

.left_title {
  font-family: var(--font-unbounded);
  font-style: normal;
  font-weight: var(--fw-300);
  font-size: 2.22vw;
  line-height: 2.78vw;
  color: var(--black-1D2E43);
}

.left_meta {
  font-style: normal;
  font-weight: var(--fw-400);
  font-size: 0.83vw;
  line-height: 1.11vw;
  font-feature-settings: 'pnum' on, 'lnum' on;
  color: var(--gray-959DA6);
  border: 1px solid;
  border-radius: 1.25vw;
  padding: 0.35vw 1.25vw;
}

.title_text {
  font-style: normal;
  font-weight: var(--fw-400);
  font-size: 1.11vw;
  line-height: 1.67vw;
  font-feature-settings: 'pnum' on, 'lnum' on;
  color: var(--black-1D2E43);
  padding-top: 1.22vw;
  padding-bottom: 2.22vw;
}

.result_container {
  display: flex;
  gap: 4.17vw;
}

.result_container_phone {
  display: none;
}

.result_number {
  font-family: var(--font-unbounded);
  font-style: normal;
  font-weight: var(--fw-300);
  font-size: 1.39vw;
  line-height: 1.94vw;
  color: var(--dark-gray-566272);
}

.result_text {
  font-style: normal;
  font-weight: var(--fw-400);
  font-size: 0.97vw;
  line-height: 1.39vw;
  font-feature-settings: 'pnum' on, 'lnum' on;
  color: (var--gray-959DA6);
}

/* << =============== правый блок контента с информацией =============== >> */

.header_content_right {
  width: 30%;
}

.local_container {
  display: flex;
  flex-wrap: nowrap;
  gap: 4.86vw;
  padding-top: 2.08vw;
}

.local_ritgh,
.local_left {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 2.39vw;
}

.local_title {
  font-style: normal;
  font-weight: var(--fw-400);
  font-size: 0.97vw;
  line-height: 1.39vw;
  font-feature-settings: 'pnum' on, 'lnum' on;
  color: var(--dark-gray-566272);
}

.local_text {
  font-style: normal;
  font-weight: var(--fw-400);
  font-size: 0.97vw;
  line-height: 1.39vw;
  font-feature-settings: 'pnum' on, 'lnum' on;
  color: var(--gray-959DA6);
}

.item:not(:last-child) {
  padding-bottom: 2.78vw;
}


.item_img {
  width: 100%;
  border-radius: 1.11vw;
}

.item_img_latest {
  border-radius: 1.11vw 1.11vw 0 0;
}


.item_photo_together {
  display: flex;
  gap: 2.08vw;
}

.quote_container,
.description_object_container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.description_object_title {
  font-family: var(--font-unbounded);
  font-style: normal;
  font-weight: var(--fw-300);
  font-size: 1.67vw;
  line-height: 2.22vw;
}

.quote_block,
.description_object_block {
  width: 70%;
}

.description_object_block {
  display: flex;
  font-size: 1.11vw;
  line-height: 1.67vw;
  flex-direction: column;
  gap: 2.78vw;
  padding-bottom: 2.78vw;
}

.quote_text {
  font-weight: var(--fw-400);
  font-size: 1.11vw;
  line-height: 1.67vw;
  padding-bottom: 30px;
}

.quote {
  background-color: var(--light-light-gray-EBECEE);
  border-radius: 16px;
  font-weight: var(--fw-400);
  font-size: 1.11vw;
  line-height: 1.67vw;
  padding: 20px;
  margin-bottom: 40px;
}

.quote_director {
  font-weight: var(--fw-400);
  font-size: 1.11vw;
  line-height: 1.39vw;
  color: var(--gray-959DA6);
  padding-top: 24px;
}

/* Стили для мобильной версии сайта блока WinePark начинаются тут */

@media screen and (max-width: 700px) {

  .container {
    margin-left: var(--mb-container-1);
    margin-right: var(--mb-container-1);
  }

  .result_container {
    display: none;
  }

  .result_container_phone {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 5.33vw;
    padding-top: 5.33vw;
  }

  .header_img_title {
    top: 9vh;
    left: 2vh;
  }

  .header_link {
    font-size: 3.73vw;
    padding: 1.67vw 3.47vw;
  }

  .header_title {
    font-size: 8.53vw;
    line-height: 10.67vw;
    padding-top: 8vw;
  }

  .header_desc {
    font-size: 3.73vw;
    line-height: 3.73vw;
  }

  .header_content {
    padding-top: 5.33vh;
    flex-direction: column;
    justify-content: normal;
  }

  .header_content_left,
  .header_content_right {
    width: auto;
  }

  .title_block {
    flex-direction: column;
    align-items: flex-start;
    gap: 0px;
  }

  .left_title {
    font-size: 5.07vw;
    line-height: 7.47vw;
  }

  .left_meta {
    font-size: 9px;
    padding: 7px 15px;
    border-radius: 34px;
    margin-top: 2.13vw;
  }

  .title_text {
    font-size: 4.27vw;
    line-height: 6.40vw;
    padding-top: 4.27vw;
  }

  .local_container {
    padding-top: 5.33vw;
    gap: 20vw;
  }

  .local_title {
    font-size: 3.73vw;
    line-height: 5.33vw;
  }

  .local_text {
    font-size: 3.73vw;
    line-height: 5.33vw;
  }

  .result_number {
    font-size: 5.33vw;
    line-height: 7.47vw;
  }

  .result_text {
    font-size: 3.73vw;
    line-height: 5.33vw;
  }
}

/* Стили для мобильной версии начинаются тут */
@media screen and (max-width: 700px) {
  .description_object_container {
    justify-content: center;
    justify-content: initial;
    align-items: initial;
  }

  .description_object_block {
    width: 100%;
  }

  .description_object_title {
    font-family: var(--font-unbounded);
    font-style: normal;
    font-weight: var(--fw-300);
    font-size: 5.33vw;
    line-height: 7.47vw;
  }

  .description_object_text1 {
    font-family: var(--font-raleway);
    font-style: normal;
    font-weight: var(--fw-400);
    font-size: 4.27vw;
    line-height: 6.40vw;
  }
}

/* +++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++++ */

/* Стили временно для кнопок */

.button_project_link {
  border: none;
  color: var(--white-F6F6F6);
  text-align: center;
  font-size: 1.4vh;
  cursor: pointer;
  font-family: 'Raleway';
  font-style: normal;
  font-weight: 400;
  line-height: 20px;
  padding: 3vh 3vw;
  }
  
.button_link::before {
  content: "<";
  /* Код символа стрелки влево */
  margin-right: 10px;
  /* Отступ между стрелкой и текстом */
}
.button_project {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  }

.previous_svg, .next_svg {
display: flex;
border: none;
justify-content: center;
align-items: center;
height: 100%;
}
.previous_span,
.next_span {
   padding-left: 0.7vw;
  font-family: var(--font-raleway);
  font-weight: var(--fw-400);
  font-size: 0.97vw;
  line-height: 1.38vh;
  color: var(--black-1D2E43);
}

.next_svg span{
padding-right: 0.7vw;
font-family: 'Raleway';
font-style: normal;
font-weight: 400;
font-size: 0.97vw;
line-height: 1.38vh;
/* color: var(--black-1D2E43); */
  }

  .button_project :hover {
   background-color: var(--salmon-light-FFBFB5);
    fill: var(--salmon-light-FFBFB5);
  }


/* Стили для мобильной версии сайта блока WinePark начинаются тут */
@media screen and (max-width: 700px) {

.button_link {
     padding: 0.5vh 3vw;
     border-radius: 2.6vw;
    }  

    .button_project {
      display: none;
    } 
}
